import * as Yup from 'yup';

const DisplayingErrorMessagesSchema = Yup.object().shape({
    username: Yup.string()
        .max(50, 'Слишком длинное имя пользователя')
        .required('Введите имя пользователя'),
    lastName: Yup.string()
        .max(50, 'Слишком длинная фамилия')
        .required('Введите фамилию'),
    firstName: Yup.string()
        .max(50, 'Слишком длинное имя')
        .required('Введите имя'),
    password: Yup.string()
        .min(8, 'Слишком короткий пароль')
        .max(50, 'Слишком длинный пароль')
        .required('Введите пароль')
        .matches(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/,
            {message: 'Слишком легкий пароль', excludeEmptyString: true }),
    passwordConfirmation: Yup.string()
        .oneOf([Yup.ref('password'), null], 'Пароли должны совпадать')
        .required('Введите пароль еще раз'),
    email: Yup.string().email('Некорректный адрес почты').required('Введите электронную почту'),
});

export default DisplayingErrorMessagesSchema;