import {userAPI} from '../api/user-api';

let initialState = {
    isUsersDownloaded: false,
    users: null,
};

const userReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'user/SET_USERS':
        case 'user/SET_IS_USERS_DOWNLOADED':
            return {
                ...state,
                ...action.payload,
            };
        default:
            return state;
    }
};

const actions = {
    setUsers: (users) => ({type: 'user/SET_USERS', payload: {users}}),
    setIsUsersDownloaded: (isUsersDownloaded) =>
        ({type: 'user/SET_IS_USERS_DOWNLOADED', payload: {isUsersDownloaded}}),
}

export const fetchUsers = () => async (dispatch) => {
    let data = await userAPI.fetchUsers();
    if (data) {
        dispatch(actions.setUsers(data));
        dispatch(actions.setIsUsersDownloaded(true));
    }
};

export default userReducer;