export default ({isLogin, values, errors}) => {
    const rules = {
        username: value => {
            if (!value) {
                errors.username = 'Введите имя ползователя';
            }
            else if (value.length > 100)
                errors.password = 'Слишком длинное имя пользователя'
        },
        lastName: value => {
            if (!value) {
                errors.username = 'Введите фамилию';
            }
            else if (value.length > 100)
                errors.password = 'Слишком длинная фамилия'
        },
        firstName: value => {
            if (!value) {
                errors.username = 'Введите имя';
            }
            else if (value.length > 100)
                errors.password = 'Слишком длинное имя'
        },
        password: value => {
            if (!value)
                errors.password = 'Введите пароль';
            else if (value.length > 100)
                errors.password = 'Слишком длинный пароль'
            // else if (регулярное выражение)
            //     errors.password = isLogin ? '' : 'Слишком простой пароль'
        }
    };

    Object.keys(values).forEach(
        key => rules[key] && rules[key](values[key]));
};